@import '../../utils/vars.scss';
@import '../../utils/sidebar__utils/sidebar__vars.scss';
@import '../../utils/mixins.scss';

.button__wrapper {
  display: block;
  /* display as block element */
  border: none;
  /* remove default border */
  background-color: transparent;
  /* remove default background */
  padding: 0;
  /* remove default padding */
  margin: 0;
  /* remove default margin */
  box-sizing: border-box;
  /* ensure element size includes padding and border */

  display: flex;
  justify-content: center;
  align-items: center;

  height: 73px;
  width: 83px;
  background-color: $base-color;
  border-radius: 12px 0px 0px 12px;
  visibility: hidden;
  cursor: pointer;
  margin-left: 7px;

  @media (min-height: 700px) {
    height: 80px;
  }

  @media (min-height: 800px) {
    height: 87px;
  }

  &_active {
    margin-left: 13px;
    // padding-right: 15px;
    visibility: visible;
  }
}

.sidebar {
  background: $base-color-dark;
  height: 100vh;
  width: 96px;
  border-top-right-radius: 16px;
  border-bottom-right-radius: 16px;
  position: fixed;

  @media (max-width: 466px) {
    display: none;
  }

  @media (max-height: 640px) {
    display: none;
  }

  &__gap {
    width: calc(100%);
    height: 19px;
    background-color: $base-color;

    &__radius {
      background-color: $base-color-dark;
      width: 100%;
      height: 20px;

      &__top_on {
        border-top-right-radius: 20px;
      }

      &__bottom_on {
        border-bottom-right-radius: 20px;
      }
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
  }

  &__logo {
    width: 55px;
    height: 55px;
    background: rgba(235, 150, 106, 0.26);
    border-radius: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
    margin-top: 20px;

    &__image {
      background-image: url('../../images/logos/main-logo.svg');
      background-repeat: no-repeat;
      background-position: center;
      width: 33px;
      height: 33px;
      opacity: 1;
    }
  }

  &__nav {


    @media (min-height: 700px) {
      margin: auto 0;
    }

    &__list {
      display: flex;
      flex-direction: column;
      justify-content: left;
    }

    &__link {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 56px;
      height: 56px;
      background-color: $main-orange;
      box-shadow: 0px 8px 24px rgba(234, 124, 105, 0.32);
      border-radius: 8px;
    }

    &__logo {
      background-repeat: no-repeat;
      background-position: center;
      width: 20px;
      height: 20px;
      visibility: visible;


      &_home {
        background-image: url('../../images/logos/home.svg');

        &_active {
          background-image: url('../../images/logos/home-active.svg')
        }
      }

      &_discount {
        background-image: url('../../images/logos/discount.svg');

        &_active {
          background-image: url('../../images/logos/discount-active.svg')
        }
      }

      &_stats {
        background-image: url('../../images/logos/admin-stats.svg');

        &_active {
          background-image: url('../../images/logos/admin-stats-active.svg')
        }
      }

      &_notification {
        background-image: url('../../images/logos/notification.svg');

        &_active {
          background-image: url('../../images/logos/notification-active.svg')
        }
      }

      &_email {
        background-image: url('../../images/logos/email.svg');

        &_active {
          background-image: url('../../images/logos/email-active.svg')
        }
      }


      &_settings {
        background-image: url('../../images/logos/settings.svg');

        &_active {
          background-image: url('../../images/logos/settings-active.svg')
        }
      }

      &_exit {
        background-image: url('../../images/logos/exit.svg');

        &_active {
          background-image: url('../../images/logos/exit-active.svg')
        }
      }
    }
  }
}

.button__logout {
  position: absolute;
  bottom: 5px;
  left: 0;
}