@import '../../../../utils/vars.scss';
@import '../../../../utils/mixins.scss';

.info {
  grid-column: 2;
  // margin-right: 8.75rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  height: calc(100vh - 48px);
  gap: 1.6875rem;
}


.order__info {
  // margin-top: 1000px;
  border-radius: 8px;
  background-color: $base-color-dark;
  padding: 1.5rem;
  padding-bottom: 0;
  padding-top: 0;
  height: 100%;

  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    border-bottom: 1px solid $border-color;

    &__title {
      height: 1.75rem;
      width: 8.0625rem;
      color: $main-font-color;
      font-family: 'Barlow';
      font-size: 1.25rem;
      font-weight: 600;
      line-height: 1.75rem;
      letter-spacing: 0em;
    }

    &__select {
      color: $main-font-color;
      background-color: $base-color-dark;
      border: 1px solid $border-color;
  
      border-radius: 8px;
      padding: 0.9375rem 0 0.9375rem 2.5rem;
  
      -moz-appearance: none;
      -webkit-appearance: none;
      appearance: none;
      outline: none;
      cursor: pointer;
  
      font-family: 'Barlow';
      font-style: normal;
      font-weight: 500;
      font-size: 0.875rem;

      &__wrapper {
        position: relative;
        // margin-right: 35px;

        &::before {
          content: '';
          position: absolute;
          top: 38%;
          left: 0.9375rem;
          transform: translateX(-50%);
          pointer-events: none;
  
          display: block;
  
          width: 0.1875rem;
          height: 0.1875rem;
  
          border: solid $main-font-color;
          border-width: 0 0.1875rem 0.1875rem 0;
          display: inline-block;
          padding: 0.1875rem;
          transform: rotate(45deg);
          -webkit-transform: rotate(45deg);
        }
      }

      &::-ms-expand {
        display: none;
      }
    }
  }

  &__main {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-top: 0.9375rem;
    height: 73%;

    &__wrapper {
      display: flex;
      flex-direction: column;
      gap: 1.6875rem;
    }

    &__button {
      align-self: center;
      width: 80%;
      height: 4rem;
  
      background-color: transparent;
      border-radius: 8px;
  
      font-family: 'Barlow';
      font-style: normal;
      font-family: 'Barlow';
      font-style: normal;
      font-weight: 600;
      font-size: 1.125rem;
      border: 1px solid $main-orange;
      color: $main-orange;
    }
  }

}

.item__information {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  &__left {
    display: flex;
    flex-direction: row;
    // margin-right: 61px;

    &__image {
      width: 4.375rem;
      height: 4.375rem;
      background-color: #fff;
      background-color: #fff;
      background: url('../../../../images/food_photos/foodPhoto.png');
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;

      border-radius: 50%;

      margin-right: 0.875rem;
    }

    &__description {
      display: flex;
      flex-direction: column;
      justify-content: center;
      gap: 0.4375rem;

      &__title {
        color: white;
      }

      &__price {
        color: gray;
      }
    }

  }

  &__right {
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: row;
    margin-right: 7px;

    &__quantity {
      color: white;

      &__wrapper {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0.875rem 0.875rem 0.75rem;

        width: 1.5rem;
        height: 1.5rem;

        background: #2D303E;

        border: 1px solid #393C49;
        border-radius: 8px;

        margin-right: 33px;
      }
    }

    &__price {
      width: 2.6875rem;
      height: 1.375rem;

      font-family: 'Barlow';
      font-style: normal;
      font-weight: 500;
      font-size: 1rem;
      line-height: 140%;

      color: #FFFFFF;
      // margin-right: 40px;
    }
  }
}

.global__info {
  border-radius: 8px;
  background-color: $base-color-dark;
  height: 100%;

  &__wrapper {
    display: flex;
    flex-direction: column;
  }

  &__header {
    padding: 1.25rem 5.125rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid $border-color;
    
    &__title {
      color: $main-font-color;
      font-family: 'Barlow';
      font-size: 1.625rem;
      font-weight: 600;
      letter-spacing: 0em;
    }

    &__select {
      color: $main-font-color;
      background-color: $base-color-dark;
      border: 1px solid $border-color;
  
      border-radius: 8px;
      padding: 0.9375rem 1.25rem 0.9375rem 2.5rem;
  
      -moz-appearance: none;
      -webkit-appearance: none;
      appearance: none;
      outline: none;
      cursor: pointer;
  
      font-family: 'Barlow';
      font-style: normal;
      font-weight: 500;
      font-size: 0.875rem;

      &__wrapper {
        position: relative;
        // margin-right: 35px;

        &::before {
          content: '';
          position: absolute;
          top: 38%;
          left: 0.9375rem;
          transform: translateX(-50%);
          pointer-events: none;
  
          display: block;
  
          width: 0.1875rem;
          height: 0.1875rem;
  
          border: solid $main-font-color;
          border-width: 0 0.1875rem 0.1875rem 0;
          display: inline-block;
          padding: 0.1875rem;
          transform: rotate(45deg);
          -webkit-transform: rotate(45deg);
        }
      }

      &::-ms-expand {
        display: none;
      }
    }
  }

  &__main {

    &__wrapper {
      padding: 2rem 2rem;
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 32px;

      height: calc(50vh - 77px);
    }

    &__graph {
      background-color: #65B0F6;
      width:  calc(50vh - 200px);
      height: calc(50vh - 200px);
      border-radius: 50%;
      margin: 0 auto;

      @media (max-width: 600px) {
        width: 200px;
        height: 200px;
      }

      @media (max-width: 466px) {
        height: 150px;
        width: 150px;
      }

      @media (max-width: 366px) {
        height: 100px;
        width: 100px;
      }
    }

    &__info {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding-top: 1rem;
      padding-bottom: 1rem;
      gap: 4rem;

      &__wrapper {
        display: flex;
      }

      &__circle {
        width: 1.5rem;
        height: 1.5rem;
        border-radius: 50%;

        &__pink {
          background-color: #FF7CA3;
        }

        &__yellow {
          background-color: #FFB572;
        }

        &__blue {
          background-color: #65B0F6;
        }
      }

      &__text {
        margin-left: 0.875rem;
        display: flex;
        flex-direction: column;

        &__title {
          font-family: 'Barlow';
          font-style: normal;
          font-weight: 500;
          font-size: 1.125rem;
          color: #FFFFFF;
        }

        &__description {
          margin-top: 0.625rem;
          font-family: 'Barlow';
          font-style: normal;
          font-weight: 400;
          font-size: 0.875rem;
          color: #ABBBC2;
        }
      }
    }
  }
}