@import '../../../../../../utils/vars.scss';

.security__page {
  margin: 24px;
  // overflow-y: scroll;
  // -ms-overflow-style: none;
  // scrollbar-width: none;
  // margin-top: 0;

  @media (max-width: 1024px) {
    // max-height: 600px;
    // margin-top: 24px;
    padding-bottom: 24px;
  }

  &__body {
    display: flex;
    gap: 24px;
    flex-direction: column;
    height: 80vh;
    // justify-content: space-between;

    @media (max-width: 1200px) {
      height: auto;
    }
  }

  // &::-webkit-scrollbar {
  //   display: none;
  // }

  &__wrapper {
    padding: 24px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 24px;

    @media (max-width: 1021px) {
      grid-template-columns: 1fr;
    }

    &__password {
      display: grid;
      grid-template-rows: 1fr 0.5fr;
      gap: 12px;
      padding: 24px;

      @media (max-width: 1021px) {
        display: block;
      }
    }
  }

  &__container {
    background-color: $base-color;
    padding: 12px;
    border-radius: 8px;

    @media (max-width: 1200px) {
      margin-top: 24px;
    }
  }

  &__text {
    font-family: 'Barlow';
    font-style: normal;
    font-weight: 500;
    font-size: 1.5625rem;
    line-height: 130%;
    color: $main-font-color;
  }

  &__left {
    display: flex;
    flex-direction: column;
    gap: 12px;

    &__password {
      display: flex;
      flex-direction: row;
      gap: 24px;
      justify-content: space-between;

      @media (max-width: 1021px) {
        display: block;
      }
    }
  }

  &__right {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }

  &__registration {
    display: flex;
    flex-direction: column;
    gap: 5px;
    width: 100%;

    &__password {
      display: flex;
      flex-direction: column;
      gap: 5px;
      width: 100%;

      @media (max-width: 1021px) {
        margin-bottom: 8px;
      }
    }

    &__email__dots {
      color: red;
    }

    &__email__text {
      font-family: 'Barlow';
      font-style: normal;
      font-weight: 500;
      font-size: 14px;
      line-height: 130%;
      color: $main-font-color;

      &__wrapper {
        display: flex;
        flex-direction: row;
        background-color: $base-color-dark;
        width: max-content;
        padding: 4px;
        border-radius: 4px;
        justify-content: center;
        align-items: center;
        gap: 4px;
      }
    }

    &__email__input {
      width: 100%;
      background-color: $base-color-dark;
      text-transform: none;
      color: white;
      border: 1px solid $border-color;
      padding: 6px;
      border-radius: 4px;
    }

    &__new__password__text {
      font-family: 'Barlow';
      font-style: normal;
      font-weight: 500;
      font-size: 14px;
      line-height: 130%;
      color: $main-font-color;
      background-color: $base-color-dark;
      width: max-content;
      padding: 4px;
      border-radius: 4px;

      @media (max-width: 470px) {
        width: auto;
      }

      @media (max-width: 620px) {
        font-size: 8px;
      }
    }

    &__new__password__input {
      width: 100%;
      background-color: $base-color-dark;
      text-transform: none;
      color: white;
      border: 1px solid $border-color;
      padding: 6px;
      border-radius: 4px;

      @media (max-width: 600px) {
        font-size: 8px;
        padding: 4px;
      }
    }
  }

  &__username {
    display: flex;
    flex-direction: column;
    gap: 5px;

    &__input {
      width: 100%;
      background-color: $base-color-dark;
      text-transform: none;
      color: white;
      border: 1px solid $border-color;
      padding: 6px;
      border-radius: 4px;
    }

    &__dots {
      color: red;
    }

    &__text {
      font-family: 'Barlow';
      font-style: normal;
      font-weight: 500;
      font-size: 14px;
      line-height: 130%;
      color: $main-font-color;

      &__wrapper {
        display: flex;
        flex-direction: row;
        background-color: $base-color-dark;
        width: max-content;
        padding: 4px;
        border-radius: 4px;
        justify-content: center;
        align-items: center;
        gap: 4px;
      }
    }
  }

  &__phone {
    display: flex;
    flex-direction: column;
    gap: 5px;

    &__input {
      width: 100%;
      background-color: $base-color-dark;
      text-transform: none;
      color: white;
      border: 1px solid $border-color;
      padding: 6px;
      border-radius: 4px;
    }

    &__dots {
      color: red;
    }

    &__text {
      font-family: 'Barlow';
      font-style: normal;
      font-weight: 500;
      font-size: 14px;
      line-height: 130%;
      color: $main-font-color;

      &__wrapper {
        display: flex;
        flex-direction: row;
        background-color: $base-color-dark;
        width: max-content;
        padding: 4px;
        border-radius: 4px;
        justify-content: center;
        align-items: center;
        gap: 4px;
      }
    }
  }

  &__card {
    display: flex;
    flex-direction: column;
    gap: 5px;

    &__input {
      width: 100%;
      background-color: $base-color-dark;
      text-transform: none;
      color: white;
      border: 1px solid $border-color;
      padding: 6px;
      border-radius: 4px;
    }

    &__dots {
      color: red;
    }

    &__text {
      font-family: 'Barlow';
      font-style: normal;
      font-weight: 500;
      font-size: 14px;
      line-height: 130%;
      color: $main-font-color;

      &__wrapper {
        display: flex;
        flex-direction: row;
        background-color: $base-color-dark;
        width: max-content;
        padding: 4px;
        border-radius: 4px;
        justify-content: center;
        align-items: center;
        gap: 4px;
      }
    }
  }

  &__confirm {
    display: flex;
    flex-direction: column;
    gap: 5px;
    width: 100%;

    @media (max-width: 1021px) {
      display: block;
      margin-bottom: 8px;
    }

    &__text {
      font-family: 'Barlow';
      font-style: normal;
      font-weight: 500;
      font-size: 14px;
      line-height: 130%;
      color: $main-font-color;
      background-color: $base-color-dark;
      width: max-content;
      padding: 4px;
      border-radius: 4px;

      @media (max-width: 470px) {
        width: auto;
      }

      @media (max-width: 620px) {
        font-size: 8px;
      }
    }

    &__input {
      width: 100%;
      background-color: $base-color-dark;
      text-transform: none;
      color: white;
      border: 1px solid $border-color;
      padding: 6px;
      border-radius: 4px;

      @media (max-width: 600px) {
        font-size: 8px;
        padding: 4px;

      }
    }
  }

  &__current__password {
    display: flex;
    flex-direction: column;
    gap: 5px;
    width: 100%;

    &__text {
      font-family: 'Barlow';
      font-style: normal;
      font-weight: 500;
      font-size: 14px;
      line-height: 130%;
      color: $main-font-color;
      background-color: $base-color-dark;
      width: max-content;
      padding: 4px;
      border-radius: 4px;

      @media (max-width: 470px) {
        width: auto;
      }

      @media (max-width: 620px) {
        font-size: 8px;
      }
    }

    &__input {
      width: 100%;
      background-color: #7eb5ed;
      opacity: 0.6;
      text-transform: none;
      color: white;
      border: 1px solid $border-color;
      padding: 6px;
      border-radius: 4px;
      font-weight: 600;
      font-size: 17px;
      display: flex;

      @media (max-width: 600px) {
        font-size: 8px;
        padding: 4px;

      }
    }
  }

  &__button {
    width: max-content;
    cursor: pointer;
    display: inline-block;
    font-weight: 400;
    font-size: 12px;
    letter-spacing: .2px;
    text-transform: uppercase;
    line-height: 1;
    padding: 9px 10px 7px;
    position: relative;
    text-align: center;
    text-decoration: none;
    border-radius: 4px;
    background-color: #6795de;
    margin: auto 0 0 0;

    @media (max-width: 1200px) {
      margin: 0;
    }
  }

  width: auto;

}