@import '../../../../utils/vars.scss';
@import '../../../../utils/mixins.scss';

.selector__bar {
  height: 85vh;
  border-radius: 8px;
  background-color: $base-color-dark;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;

  @media (min-width: 1024px) and (max-width: 1200px) {
    margin-bottom: 24px;
    margin: 0 42px 0 24px;
  }

  @media (max-width: 1024px) {
    // margin-bottom: 24px;
    margin: 0 24px;
    // max-width: calc(100% - 48px);
    // margin: 0 42px 24px 24px;
  }


  &__item {
    width: 100%;

    &__wrapper {
      outline: none;
      padding: 48px 24px;
      display: flex;
      gap: 8px;
      // align-items: center;
    }

    &__logo {
      width: 18px;
      height: 19px;
      background-repeat: no-repeat;
      background-size: cover;

      &_heart {
        background-image: url('../../SettingsImage/inactive/heart.svg');
      }

      &_restaurant {
        background-image: url('../../SettingsImage/inactive/restaurant.svg');
      }

      &_discount {
        background-image: url('../../SettingsImage/inactive/discount.svg');
      }

      &_notification {
        background-image: url('../../SettingsImage/inactive/notification.svg');
        width: 14px;
        height: 16px;
      }

      &_security {
        background-image: url('../../SettingsImage/inactive/security.svg');
        width: 14px;
        height: 16px;
      }

      &_warning {
        background-image: url('../../SettingsImage/inactive/warning.svg');
      }
    }

    &__info {
      display: flex;
      flex-direction: column;
      gap: 8px;

      &__title {
        font-family: 'Barlow';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        color: #FFFFFF;
        display: flex;
        align-items: flex-start;
      }

      &__subtitle {
        font-family: 'Barlow';
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        color: #ABBBC2;
      }
    }

    &__selected {
      background: rgba(234, 124, 105, 0.26);
      width: 100%;


      &__wrapper {
        padding: 48px 24px;
        display: flex;
        gap: 8px;
      }

      &__logo {
        width: 18px;
        height: 19px;    
        background-image: url('../../SettingsImage/inactive/heart.svg');
        background-repeat: no-repeat;
        background-size: cover;

          &_heart {
            background-image: url('../../SettingsImage/active/heart.svg');
          }
    
          &_restaurant {
            background-image: url('../../SettingsImage/active/restaurant.svg');
          }
    
          &_discount {
            background-image: url('../../SettingsImage/active/discount.svg');
          }

          &_notification {
            background-image: url('../../SettingsImage/active/notification.svg');
            width: 14px;
            height: 16px;
          }

          &_security {
            background-image: url('../../SettingsImage/active/security.svg');
          }

          &_warning {
            background-image: url('../../SettingsImage/active/warning.svg');
          }
      }
  
      &__info {
        display: flex;
        flex-direction: column;
        gap: 8px;
  
        &__title {
          font-family: 'Barlow';
          font-style: normal;
          font-weight: 500;
          font-size: 16px;
          color: #EA7C69;
          display: flex;
          align-items: flex-start;
        }
  
        &__subtitle {
          font-family: 'Barlow';
          font-style: normal;
          font-weight: 400;
          font-size: 12px;
          color: #ABBBC2;
        }
      }
    }
  }
}