@import '../../../../utils/mixins.scss';
@import '../../../../utils/vars.scss';

.menu {
  margin-top: 1.5rem;
  display: flex;
  justify-content: space-between;

  &__title {
    height: 1.75rem;
    width: 8.0625rem;
    color: $main-font-color;
    font-family: 'Barlow';
    font-size: 1.25rem;
    font-weight: 600;
    line-height: 1.75rem;
    letter-spacing: 0em;
  }

  &__select {
    color: $main-font-color;
    background-color: $base-color-dark;
    border: 1px solid $border-color;

    border-radius: 8px;
    padding: 0.9375rem 1.25rem 0.9375rem 2.5rem;

    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    outline: none;
    cursor: pointer;

    font-family: 'Barlow';
    font-style: normal;
    font-weight: 500;
    font-size: 0.875rem;
    line-height: 130%;

    &__wrapper {
      position: relative;
      margin-right: 2.1875rem;

      &::before {
        content: '';
        position: absolute;
        top: 38%;
        left: 0.9375rem;
        transform: translateX(-50%);
        pointer-events: none;

        display: block;

        width: 0.1875rem;
        height: 0.1875rem;

        border: solid $main-font-color;
        border-width: 0 0.1875rem 0.1875rem 0;
        display: inline-block;
        padding: 0.1875rem;
        transform: rotate(45deg);
        -webkit-transform: rotate(45deg);
      }
    }

    &::-ms-expand {
      display: none;
    }
  }

  &__portfolio {
    margin-top: 6rem;
    display: grid;
    gap: 4.475rem;
    justify-content: center;
    grid-template-columns: repeat(6, 1fr);
    margin-bottom: 50px;

    @include onDesktop {
      grid-template-columns: repeat(3, 1fr);
      gap: 3.475rem;
    }

    @media (min-width: 1025px) and (max-width: 1250px) {
      grid-template-columns: repeat(2, 1fr);
      gap: 3.475rem;
    }

    @include onTablet {
      grid-template-columns: repeat(2, 1fr);
      gap: 3.475rem;
    }

    @media (min-width: 768px) and (max-width: 890px) {
      grid-template-columns: repeat(2, 1fr);
      gap: 3.475rem;
    }

    @include onMobile {
      grid-template-columns: repeat(1, 1fr);
      gap: 2.475rem;
    }
  }

  &__portfolio_order {
    margin-top: 6rem;
    display: grid;
    gap: 4.475rem;
    justify-content: center;
    grid-template-columns: repeat(4, 1fr);

    @include onDesktop {
      grid-template-columns: repeat(3, 1fr);
      gap: 3.475rem;
    }

    @include onTablet {
      grid-template-columns: repeat(2, 1fr);
      gap: 3.475rem;
    }
  }
}

.loader__wrapper {
  // display: flex;
  // justify-content: center;
  // align-items: center;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}