@import '../../../../utils/mixins.scss';
@import '../../../../utils/vars.scss';

.dashboard__stats {
  &__header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid $border-color;

    &__button {
      margin-bottom: 15px;
    }

    &__info {


      &__title {
        @include default-font-style;
        font-size: 28px;
        color: $main-font-color;
      }

      &__time {
        font-family: 'Barlow';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 140%;
        color: #ABBBC2;
        padding-bottom: 24px;
      }
    }
  }

  &__info {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    flex-wrap: wrap;
    gap: 20px;

    margin: 24px 0;

    &__item {

      border-radius: 8px;
      width: 190px;
      height: 100%;
      background-color: $base-color-dark;
      padding: 24px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      gap: 12px;

      @media (max-width: 543px) {
        margin: 0 auto;
      }

      &__percent {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;

        &__logo {
          width: 38px;
          height: 38px;
          background: #252836;
          border-radius: 8px;
          margin-right: 12px;

          &__dollar {
            background-image: url('../../../../images/logos/dollar-sign.svg');
            background-repeat: no-repeat;
            background-position: center;
            width: 100%;
            height: 100%;
          }

          &__bookmark {
            background-image: url('../../../../images/logos/bookmark.svg');
            background-repeat: no-repeat;
            background-position: center;
            width: 100%;
            height: 100%;
          }

          &__people {
            background-image: url('../../../../images/logos/people.svg');
            background-repeat: no-repeat;
            background-position: center;
            width: 100%;
            height: 100%;
          }

          &__people {
            background-image: url('../../../../images/logos/people.svg');
            background-repeat: no-repeat;
            background-position: center;
            width: 100%;
            height: 100%;
          }

          &__people {
            background-image: url('../../../../images/logos/people.svg');
            background-repeat: no-repeat;
            background-position: center;
            width: 100%;
            height: 100%;
          }

          &__increasing__trend {
            background-image: url('../../../../images/logos/increasing-trend.svg');
            background-repeat: no-repeat;
            background-position: center;
            width: 100%;
            height: 100%;
          }
        }

        &__direction {
          width: 22px;
          height: 22px;
          border-radius: 50%;
          background-color: rgba(136, 216, 224, 0.2);

          &_down {
            background-color: rgba(255, 100, 113, 0.24);
          }

          &__arrow {
            background-repeat: no-repeat;
            background-position: center;
            width: 100%;
            height: 100%;

            &_down {
              background-image: url('../../../../images/logos/arrow-down.svg');
            }

            &_up {
              background-image: url('../../../../images/logos/arrow-up.svg');
            }
          }
        }

        &__value {
          font-family: 'Barlow';
          font-style: normal;
          font-weight: 500;
          font-size: 14px;
          line-height: 140%;
          margin-right: 6px;

          color: #50D1AA;
        }
      }

      &__total {
        font-family: 'Barlow';
        font-style: normal;
        font-weight: 600;
        font-size: 32px;
        line-height: 140%;
        color: $main-font-color;
        width: 135px;
        height: 39px;
      }

      &__title {
        font-family: 'Barlow';
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 140%;
        color: $main-font-color-light;
      }
    }
  }
}

.dashboard__order_report {
  overflow: hidden;
  // min-width: 400px;
  width: 100%;
  border-radius: 8px;
  background-color: $base-color-dark;
  height: calc(100vh - 355px);
  border-collapse: collapse;
  // overflow-y: scroll;
  // -ms-overflow-style: none;
  // scrollbar-width: none;


  // &::-webkit-scrollbar {
  //   display: none;
  // }

  @media (max-width: 1347px) {
    // height: calc(100vh - 535px);
    // height: 800px;
  }

  @media (max-width: 910px) {
    // height: 370px;
    margin-bottom: 1.6875rem;
    width: calc(100vw - 48px - 96px);
  }

  @media (max-width: 466px) {
    width: calc(100vw - 48px);
  }

  &_wrapper {
    padding: 2.5rem 1.5625rem 0 1.5625rem;
  }

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    // margin-right: 2.1875rem;

    &__title {
      font-family: 'Barlow';
      font-style: normal;
      font-weight: 600;
      font-size: 1.6rem;
      color: #FFFFFF;
    }

    &__filter_order {
      height: 40px;
      width: 99px;
      color: $main-font-color;
      background-color: $base-color-dark;
      border: 1px solid $border-color;

      border-radius: 8px;
      padding: 9px 0.75rem 8px 2.5rem;

      -moz-appearance: none;
      -webkit-appearance: none;
      appearance: none;
      outline: none;
      cursor: pointer;

      font-family: 'Barlow';
      font-style: normal;
      font-weight: 500;
      font-size: 0.875rem;
      line-height: 130%;

      &_wrapper {
        position: relative;

        &::before {
          content: '';
          position: absolute;
          top: 38%;
          left: 0.9375rem;
          transform: translateX(-50%);
          pointer-events: none;

          display: block;

          width: 0.1875rem;
          height: 0.1875rem;

          border: solid $main-font-color;
          border-width: 0 0.1875rem 0.1875rem 0;
          display: inline-block;
          padding: 0.1875rem;
          transform: rotate(45deg);
          -webkit-transform: rotate(45deg);
        }
      }
    }
  }
}

.dashboard__container {
  // display: flex;
  // max-width: fit-content;
  max-height: fit-content;
  // max-width: fit-content;
}

.dashboard__table {
  border-collapse: collapse;
  width: 100%;
  overflow-x: auto;
  margin: 25px 0;
  font-size: 0.9em;
  // width: 100%; 
  position: relative;
  min-width: max-content;
  border-collapse: separate;

  &__fixed {
    overflow-y: scroll;
    // display: flex;

    height: fit-content;
    max-height: 70vh;
    // min-width: fit-content;
    // width: 80vw;

    position: relative;

    // -ms-overflow-style: none;
    // scrollbar-width: none;

    // &::-webkit-scrollbar{
    //   display: none;
    // }

    // @media (max-width: 910px) {
    //   max-height: 300px;
    // }
  }

  &__head {
    // position: sticky;
    // top: 0;
    font-family: 'Barlow';
    font-style: normal;
    font-weight: 600;
    font-size: 1rem;
    color: #ffffff;
    text-align: left;
    font-weight: bold;
    background-color: #1F1D2B;

    &__row {
      border-bottom: 1px solid $border-color;

      &__item {
        padding: 12px 15px;
        padding-top: 0;
        position: sticky;
        top: 0;
        background-color: $base-color-dark;
      }
    }
  }

  &__body {

    &__row {
      // border-bottom: 1px solid #dddddd;

      &__item {
        padding: 12px 15px;

        &__wrapper {
          display: flex;
          align-items: center;
          gap: 0.75rem;
        }

        &__avatar {
          &__img {
            width: 2.1875rem;
            height: 2.1875rem;
            border-radius: 50%;
          }
        }

        &__name {
          font-family: 'Barlow';
          font-style: normal;
          font-weight: 400;
          font-size: 1.125rem;
          color: #E0E6E9;
        }

        &__menu {
          &__name {
            font-family: 'Barlow';
            font-style: normal;
            font-weight: 400;
            font-size: 1.125rem;
            color: #E0E6E9;
          }
        }

        &__payment {
          &__total {
            font-family: 'Barlow';
            font-style: normal;
            font-weight: 400;
            font-size: 1.125rem;
            color: #E0E6E9;
          }
        }

        &__status {
          &__circle {
            padding: 4px 12px;
            background: rgba(107, 226, 190, 0.24);
            border-radius: 30px;
            width: 5rem;
            display: flex;
            justify-content: center;

            &__text {
              font-family: 'Barlow';
              font-style: normal;
              font-weight: 500;
              font-size: 0.875rem;
              color: #50D1AA;
            }
          }
        }
      }

    }
  }
}