@import '../../utils/vars.scss';
@import '../../utils/mixins.scss';

.burger_menu {
  padding: 32px;
  height: calc(100vh - 70px);

  &__header {
    padding-bottom: 40px;
    border-bottom: 1px solid $border-color;

    &__close {
      display: flex;
      justify-content: space-between;
      align-items: center;
      // margin-bottom: 48px;
      // margin-top: 80px;
    }

    &__user__info {
      display: flex;
      justify-content: left;
      align-items: center;
      gap: 24px;

      &__img {
        width: 80px;
        height: 80px;
        border-radius: 50%;
        background-color: #fff;
        border: 4px solid pink;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
      }

      &__name {
        @include default-font-style;
        font-size: 24px;
        line-height: 140%;
        color: $main-font-color;
        white-space: nowrap;
      }
    }

    &__title {
      @include default-font-style;
      font-size: 24px;
      line-height: 140%;
      color: $main-font-color;
    }

    &__logo_close {
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      width: 18px;
      height: 18px;
      cursor: pointer;
      background-image: url(../../images/logos/close-bgc.svg);

      outline: none;
      border: none;
      background-color: transparent;
    }
  }

  &__main {
    margin-top: 24px;
    padding-bottom: 40px;
    border-bottom: 1px solid $border-color;

    &__ul {
      display: flex;
      flex-direction: column;
      gap: 32px;

      &__item {
        display: flex;
        gap: 10px;
        align-items: center;

        &__active {
          background-color: #747db3;
          padding: 4px 10px;
          border-radius: 8px;
          width: 50%;
          margin-left: 9px;
          max-width: 150px;
        }

        &__container {
          display: flex;
        }

        &__notification {
          // justify-content: space-between;

          &__count {
            @include default-font-style;
            font-size: 12px;
            color: $main-font-color;
            background-color: #747db3;
            border-radius: 30px;
            // width: 25px;
            // height: 25px;
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 4px 8px;
          }
        }

        &__wrapper {
          display: flex;
          align-items: center;
          gap: 50px;
        }

        &__link {
          @include default-font-style;
          font-size: 16px;
          color: $main-font-color-light;
          text-decoration: none;
          cursor: pointer;

          &__exit {
            color: #ff6863;
          }
        }

        &__img {
          width: 16px;
          height: 16px;
          background-repeat: no-repeat;
          background-size: contain;
          background-position: center;

          &__home {
            background-image: url('../../images/logos/home-active.svg');
          }

          &__dashboard {
            background-image: url('../../images/logos/admin-stats-active.svg');
          }

          &__notification {
            background-image: url('../../images/logos/notification-active.svg');
          }

          &__settings {
            background-image: url('../../images/logos/settings-active.svg');
          }

          &__exit {
            background-image: url('../../images/logos/exit-red.svg');
          }
        }
      }
    }
  }

  &__footer {
    margin-top: 24px;
  }
}