@import '../../utils/vars.scss';
@import '../../utils/mixins.scss';

.product__card {
  background-color: $base-color-dark;
  border-radius: 8px;
  width: 100%;
  padding-top: .625rem;
  // min-height: 200px;

  &_container {
    padding: .8125rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 50px;
    cursor: pointer;
    height: 100%;

    @include onMobile {
      gap: 0;
    }

    @include onTablet {
      gap: 20px;
    }
  }

  &_left {
    display: flex;
    flex-direction: column;
    // justify-content: flex-end;

    &__label {
      margin-top: .625rem;
      display: flex;
      align-items: center;
      margin-bottom: 20px;

      &__weight {
        background-image: url('../../images/logos/product_weight.svg');
        background-repeat: no-repeat;
        background-size: cover;
        width: 1.25rem;
        height: 1.25rem;
      }

      &__text {
        margin-left: 5px;
        font-family: 'Barlow';
        font-style: normal;
        font-weight: 300;
        font-size: 12px;
        line-height: 100%;
        color: #fff;
      }
    }

    &__description {
      margin-top: 15px;

      font-family: 'Barlow';
      font-style: normal;
      font-weight: 300;
      font-size: 1.3rem;
      color: #ffffffb8;

      @include onMobile {
        font-size: 18px;
      }

      @include onMobileMini {
        font-size: 12px;
      }
    }


    &__name {
      font-family: 'Barlow';
      font-style: normal;
      font-weight: 600;
      font-size: 27px;
      line-height: 100%;
      color: #fff;
      // white-space: nowrap;
      // max-width: 110px;

      @include onMobile {
        font-size: 20px;
      }
    }

    &__price {
      margin-top: 6px;

      font-family: 'Barlow';
      font-style: normal;
      font-weight: 500;
      font-size: 22px;
      color: $main-orange;

      @include onMobile {
        font-size: 18px;
      }
    }

    &__button {
      border: none;
      border-radius: 7px;
      border: 1px solid $border-color;
      box-shadow: 0 4px 4px rgba(80, 78, 78, 0.1);
      background-color: $base-color;
      // margin-top: 14px;
      padding: 5px 5px;
      width: 50px;
      height: 30px;
      margin: auto 0 0 0;

      &_wrapper {
        display: flex;
        flex-direction: row;
        align-items: center;
        margin-top: 2px;
      }


      &__like_icon {
        width: 18px;
        height: 18px;
        margin-right: 10px;
      }

      &__like_count {
        font-family: 'Barlow';
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        color: #fff;
        margin-bottom: 3px;
        margin-right: 5px;
      }
    }
  }

  &__right {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 10px;
    width: 50%;
    align-items: flex-end;


    &__image {
      height: 120px;
      width: 120px;

      border-radius: 50%;

      // @include onTablet {
      //   height: 100px;
      //   width: 100px;
      // }

      @include onMobile {
        height: 100px;
        width: 100px;
      }

      @include onMobileMini {
        height: 90px;
        width: 90px;
        // margin: 20px 5px 0 0;
      }
    }

    &__button {
      // align-self: flex-end;
      margin-top: 10px;
      width: 60px;
      padding: 10px 0 10px 0;

      border: solid 1px #da510f;
      border-radius: 5px;
      background-color: $base-color;

      display: flex;
      align-items: center;
      justify-content: center;

      transition: 0.2s ease-in-out;

      &__add_icon {
        background-image: url('../../images/logos/plus_orange.svg');
        background-repeat: no-repeat;
        background-size: cover;
        width: .9375rem;
        height: .9375rem;
      }

      &__text {
        font-family: 'Barlow';
        font-style: normal;
        font-weight: 600;
        font-size: 1.125rem;
        color: #da510f;
        margin-left: 8px;
        white-space: nowrap;
      }

      &:hover {
        background-color: #da510f;
        border: solid 1px #da510f;

        .product__card__right__button__text {
          color: #fff;
        }

        .product__card__right__button__add_icon {
          background-image: url('../../images/logos/product_plus_white.svg');
        }
      }
    }
  }
}

.button__add {
  display: flex;
  align-items: center;
  gap: 10px;
  // align-self: flex-end;

  &__minus {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #da510f;
    border-radius: 8px;
    padding: 4px;
  }

  &__count {
    font-family: 'Barlow';
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    color: #fff;
  }

  &__plus {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #da510f;
    border-radius: 8px;
    padding: 4px;

    &__icon {
      background-image: url('../../images/logos/plus-white.svg');
      background-repeat: no-repeat;
      background-size: cover;
      width: 20px;
      height: 20px;
    }
  }

  &__delete {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #da510f;
    border-radius: 8px;
    padding: 4px;

    &__icon {
      background-image: url('../../images/logos/trash-icon.svg');
      background-repeat: no-repeat;
      background-size: cover;
      width: 20px;
      height: 20px;
    }
  }
}