@mixin default-font-style {
  font-family: 'Barlow';
  font-style: normal;
  font-weight: 600;
  line-height: 140%;
}

@mixin onMobileMini {
  @media (max-width: 467px) {
    @content;
  }
}

@mixin onMobileMax {
  @media (min-width: 467px) and (max-width: 766px) {
    @content;
  }
}

@mixin onMobile {
  @media (max-width: 767px) {
    @content;
  }
}

@mixin onTablet {
  @media (min-width: 768px) and (max-width: 1024px) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: 1024px) {
    @content;
  }
}