@import '../../utils/vars.scss';
@import '../../utils/mixins.scss';

.container {
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  z-index: 5;
  overflow: auto;
}

.product_modal {
  position: fixed;
  bottom: 0;
  width: 100vw;
  height: 100%;
  background-color: $modal-background-color;
  z-index: 3;
  border-top-right-radius: 15px;
  border-top-left-radius: 15px;

  @media (min-width: 466px) {
    width: 400px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
  }

  &__img {
    width: 100%;
    height: 300px;
    object-fit: cover;
    background-image: url('../../images/food_photos/foodPhotoMain.jpg');
    background-size: cover;
    background-position: center;
    border-top-right-radius: 15px;
    border-top-left-radius: 15px;

    border-bottom: 2px solid #fff;
  }

  &__close {
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
    background-color: $main-orange;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    opacity: 0.9;

    background-image: url('../../images/logos/close-black.svg');
    background-repeat: no-repeat;
    background-position: center;
  }

  &__content {
    padding: 32px 32px 0 32px;
    // height: 100%;

    &__description {
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      &__info {}

      &__text {
        @include default-font-style;
        font-size: 16px;
        font-weight: 400;
        color: $main-font-color-light;
        margin-bottom: 16px;
      }

      &__weight {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 4px;

        &__text {
          font-size: 16px;
          font-weight: 400;
          color: #393C49;
          margin-right: 16px;
        }
      }

      &__buttons {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        padding: 0 32px;
        justify-self: flex-end;
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 16px;
        margin-top: 16px;
        flex: 1 0.6;
        margin-bottom: 16px;

        &__add {
          @include default-font-style;
          display: flex;
          flex-direction: row;
          align-items: center;
          gap: 4px;
          padding: 8px 16px;
          border-radius: 4px;
          background-color: $main-orange;
          cursor: pointer;
          width: 100%;
        }

        &__like {
          @include default-font-style;
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: center;
          gap: 4px;
          padding: 8px 16px;
          border-radius: 4px;
          background-color: $base-color;
          cursor: pointer;
          width: 15%;
        }
      }
    }

    &__title {
      font-size: 24px;
      font-weight: 600;
      color: $main-creamy-font-color;
      margin-bottom: 16px;
    }

    &__price {
      font-size: 24px;
      font-weight: 600;
      color: $main-orange;
      margin-bottom: 16px;
      margin-top: 16px;
      margin-bottom: 16px;
    }
  }
}