@import './utils/vars.scss';
@import './utils/sidebar__utils/sidebar__vars.scss';


html {
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
  box-sizing: border-box;
}

body {
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
}

.app {
  background-color: $base-color;
  min-height: 100vh;
  min-width: 100vw;
  font-family: 'Barlow', sans-serif;

  &__wrapper {
    display: grid;
    flex-direction: row;
    grid-template-columns: 96px 1fr;

    @media (max-width: 466px) {
      grid-template-columns: 1fr;
    }

    @media (max-height: 630px) {
      grid-template-columns: 1fr;
    }
  }
}
