@import '../../../../utils/vars.scss';
@import '../../../../utils/mixins.scss';


.order__mobile {
  min-height: 100vh;
  background-color: $base-color;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  gap: 24px;
  // width: 100vw;

  @media (max-width: 768px) {
    background-color: $base-color-dark;
  }

  &__main {

    &__container {
      width: 600px;
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      // border: 1px solid #393d40;
      border-radius: 8px;
      margin-bottom: 24px;
      // border-bottom: none;
      background-color: $base-color-dark;

      @media (max-width: 768px) {
        width: 100%;
      }
    }

    &__price {
      background-color: #2c2c41;
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 24px;
      border-bottom: 1px solid $border-color;

      &__text {
        font-family: 'Barlow';
        font-style: normal;
        font-weight: 500;
        font-size: 2.3rem;
        line-height: 100%;
        color: #fff;
      }

      &__count {
        font-family: 'Barlow';
        font-style: normal;
        font-weight: 500;
        font-size: 2.3rem;
        line-height: 100%;
        color: #f17337;
      }
    }

    &__cutler {
      display: flex;
      background-color: $base-color-dark;
      width: 100%;
      padding: 24px;
      justify-content: space-between;
      border-bottom: 1px solid $border-color;

      &__container {
        display: flex;
        flex-direction: column;
      }

      &__wrapper {
        display: flex;
        align-items: center;
        gap: 10px;
      }

      &__logo {
        background-image: url('../../../../images/logos/cutler.svg');
        width: 24px;
        height: 24px;
        background-repeat: no-repeat;
        background-size: cover;
      }

      &__text {
        font-family: 'Barlow';
        font-style: normal;
        font-weight: 500;
        font-size: 1.4rem;
        color: white;
      }

      &__description {
        font-family: 'Barlow';
        font-style: normal;
        font-weight: 500;
        font-size: 1rem;
        line-height: 100%;
        color: gray;
      }

      &__button {
        background-color: $base-color;
        padding: 10px;
        border: 1px solid $border-color;
        border-radius: 8px;


        &__logo {
          background-image: url('../../../../images/logos/plus-white.svg');
          width: 24px;
          height: 24px;
          background-repeat: no-repeat;
          background-size: cover;
        }
      }
    }

    &__actions {
      background-color: $base-color-dark;
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      gap: 10px;
      padding: 24px;
      border-bottom: 1px solid $border-color;

      &__button {
        background-color: $base-color;
        padding: 10px;
        border: 1px solid $border-color;
        border-radius: 8px;
        width: 100%;
        display: flex;
        gap: 12px;
        align-items: center;

        &__text {
          font-family: 'Barlow';
          font-style: normal;
          font-weight: 500;
          font-size: 1.4rem;
          line-height: 100%;
          color: white;
        }

        &__logo {
          background-image: url('../../../../images/logos/plus-white.svg');
          width: 18px;
          height: 18px;
          background-repeat: no-repeat;
          background-size: cover;
        }
      }
    }

    &__button {
      width: 90%;
      height: 40px;
      border-radius: 8px;
      background-color: #da510f;
      position: sticky;
      bottom: 24px;
      // width: 100%;
      border-bottom-left-radius: 8px;
      border-bottom-right-radius: 8px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 24px;
      padding-top: 0;


      &__text {
        font-family: 'Barlow';
        font-style: normal;
        font-weight: 500;
        font-size: 1.4rem;
        line-height: 100%;
        color: white;
      }
    }
  }

  &__header {
    width: 100%;
    padding: 24px;
    position: sticky;
    top: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    background-color: $base-color-dark;
    gap: 18px;

    &__text {


      &__main {
        font-family: 'Barlow';
        font-style: normal;
        font-weight: 500;
        font-size: 1.8rem;
        line-height: 100%;
        color: white;
      }

      &__second {
        font-family: 'Barlow';
        font-style: normal;
        font-weight: 500;
        font-size: 1.4rem;
        line-height: 100%;
        color: gray;
      }
    }

    &__back {
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #da510f;
      padding: 6px 10px;
      border-radius: 8px;

      &__image {
        background-image: url('../../../../images/logos/arrow-left-white.svg');
        width: 24px;
        height: 24px;
        background-repeat: no-repeat;
        background-size: cover;

      }
    }
  }
}


