.mobile__product__card {
  background-color: $base-color-dark;
  // border-radius: 8px;
  width: 100%;
  padding-top: .625rem;
  height: max-content;
  border-bottom: 1px solid $border-color;

  &:first-child {
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
  }

  // &:last-child {
  //   border-bottom-left-radius: 8px;
  //   border-bottom-right-radius: 8px;
  //   margin-bottom: 24px;
  // }

  &_container {
    padding: 15px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 50px;
    cursor: pointer;
  }

  &_left {

    &_label {
      margin-top: .625rem;
      display: flex;
      align-items: center;

      &_weight {
        background-image: url('../../../../images/logos/product_weight.svg');
        background-repeat: no-repeat;
        background-size: cover;
        width: 1.8rem;
        height: 1.8rem;
      }

      &_text {
        margin-left: 5px;
        font-family: 'Barlow';
        font-style: normal;
        font-weight: 300;
        font-size: 1.4rem;
        line-height: 100%;
        color: #fff;
      }
    }

    &_info {
      margin-top: 15px;

      font-family: 'Barlow';
      font-style: normal;
      font-weight: 300;
      font-size: 1.5rem;
      color: #ffffffb8;

      // @include onMobile {
      //   font-size: 1rem;
      // }
    }


    &_description {
      font-family: 'Barlow';
      font-style: normal;
      font-weight: 600;
      font-size: 1.7rem;
      line-height: 100%;
      color: #fff;
      white-space: nowrap;
      max-width: 110px;

      // @include onMobile {
      //   font-size: 1.3rem;
      // }
    }

    &_price {
      margin-top: 6px;

      font-family: 'Barlow';
      font-style: normal;
      font-weight: 500;
      font-size: 2.1rem;
      color: $main-orange;

      // @include onMobile {
      //   font-size: 1rem;
      // }
    }

    &_button {
      border: none;
      border-radius: 7px;
      border: 1px solid $border-color;
      ;
      box-shadow: 0 4px 4px rgba(80, 78, 78, 0.1);
      background-color: $base-color;
      margin-top: 14px;
      padding: 0;

      &_wrapper {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        padding: .625rem .625rem;

        @include onMobile {
          padding: .5rem .5rem;
        }
      }


      &_like_icon {
        background-image: url('../../../../images/logos/heart-icon.svg');
        background-repeat: no-repeat;
        background-size: cover;
        width: 1.5rem;
        height: 1.5rem;
        margin-right: 10px;

        @include onMobile {
          width: 1rem;
          height: 1rem;
        }
      }

      &_like_count {
        font-family: 'Barlow';
        font-style: normal;
        font-weight: 300;
        font-size: 12px;
        color: #fff;
      }
    }
  }

  &_right {
    display: flex;
    flex-direction: column;
    justify-content: space-between;


    &_image {
      height: 9rem;
      width: 9rem;
      background-color: #fff;
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      display: flex;
      align-self: center;

      border-radius: 50%;
    }

    &_button {
      // padding: 15px 20px;
      align-self: center;
      margin-top: 10px;
      width: 70%;
      padding: 10px 0 10px 0;

      border: solid 1px #da510f;
      border-radius: 5px;
      background-color: $base-color;

      display: flex;
      align-items: center;
      justify-content: center;

      &_add_icon {
        background-image: url('../../../../images/logos/plus_orange.svg');
        background-repeat: no-repeat;
        background-size: cover;
        width: 0.9375rem;
        height: 0.9375rem;
      }

      &_text {
        font-family: 'Barlow';
        font-style: normal;
        font-weight: 600;
        font-size: 1.125rem;
        color: #da510f;
        margin-left: 0.5rem;
        white-space: nowrap;
      }

      &:hover {
        background-color: #da510f;
        border: solid 1px #da510f;

        .mobile__product__card_right_button_text {
          color: #fff;
        }

        .mobile__product__card_right_button_add_icon {
          background-image: url('../../../../images/logos/product_plus_white.svg');
        }
      }
    }
  }
}

.button__add {
  display: flex;
  align-items: center;
  gap: 10px;

  &__minus {
    background-color: #fff;
    width: 50px;
    height: 20px;
  }

  &__count {
    font-family: 'Barlow';
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    color: #fff;
  }

  &__plus {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #da510f;
    border-radius: 8px;
    padding: 4px;

    &__icon {
      background-image: url('../../../../images/logos/plus-white.svg');
      background-repeat: no-repeat;
      background-size: cover;
      width: 20px;
      height: 20px;
    }
  }

  &__delete {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #da510f;
    border-radius: 8px;
    padding: 4px;

    &__icon {
      background-image: url('../../../../images/logos/trash-icon.svg');
      background-repeat: no-repeat;
      background-size: cover;
      width: 20px;
      height: 20px;
    }
  }
}